@import "./GlobalVariables.scss";

$padding: 4px;
$offsetTop: $navbarHeight + $padding;
$height: calc(100vh - ($offsetTop + $padding));

.page-read {
    position: fixed;
    left: $padding;
    bottom: 0;
    height: $height;
    z-index: 100;


    pointer-events: none;

    .line {
        stroke: $buttonColor;
        stroke-width: 6px;
        stroke-linecap: round;
    }

}

